<template>
  <div>
    <div class="bg">
      <div class="Container">
        <form class="login-form" action="login_validate.htm" method="get">
          <section>
            <label for="user_name">账户:</label>
            <input type="text" id="user_name" name="user_name" placeholder="输入学号或邮箱">
          </section>
          <section>
            <label for="user_pwd">密码:</label>
            <input type="text" id="user_pwd" name="user_pwd" placeholder="">
          </section>
          <section>
            <input type="submit" text="登陆">
            <a href="#"><span>注册</span></a>
          </section>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'loginmain'
}


</script>
<style scoped>
.bg {
  height: 500px;
  background: url("../assets/img/bg.jpg") center no-repeat;
  background-size: cover;
}

.bg .Container {
  width: 960px;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.bg .Container .login-form {
  font-size: 16px;
  /*float: right;*/
  padding: 10px 16px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 10px;
  box-shadow: 0 0 2px black;
  position: absolute;
  top: 50%;
  right: 16px;
  margin-top: -120px;
}

.bg .Container .login-form section {
  padding: 5px 10px;
}

.bg .Container .login-form section label:first-child {
  margin-right: 10px;
}

.bg .Container .login-form section input:last-child {
  padding: 5px;
}

.bg .Container .login-form section a {
  text-decoration: none;
  color: #555;
  margin: 0 5px;
  font-size: 0.8rem;
}

@media screen and (max-width:960px) {
  .bg .Container {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    position: relative;
  }
}

@media screen and (max-width:40em) {
  .bg .Container {
    padding-top: 50px;
  }
  .bg .Container .login-form {
    position: static;
    /*float: left;*/
    margin: 10px 16px;
  }
  .bg .Container .login-form section {
    margin: 5px auto;
  }
  .bg .Container .login-form section input:last-child {
    width: 100%;
    margin-top: 10px;
  }
}

</style>



